import React from 'react';
import { BarreRecherche } from './Composent/BarreRecherche';
import GrilleDeJeux from './Composent/GrilleDeJeux';
import { BoiteText } from './Composent/BoiteText';

export class Accueil extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            resultat: [],
            deuxiemeRequeteDataJeu: [],
            rechercheEnCours: "",
            afficherBoiteText: false,
            index: 0, 
        };

        // Initialisation de l'instance RechercheJeu avec le client SGDB
        this.rechercheJeu = props.rechercheJeu;
        this.getGrilleJeuxId = props.getGrilleJeuxId;
        this.client = props.client; 
    }

    fermerBoite = () => {
        this.setState({ afficherBoiteText: false });
    };
    
    // Fonction pour grer la recherche de jeux
    handleSearch = async (query) => {
        this.setState({ afficherBoiteText: true });
        await this.rechercheJeu.handleSearch(query, (newState) => this.setState(newState));
    }

    // Fonction pour grer le clic en dehors de la bote de texte
    handleClickOutside = (event) => {
        if (this.state.afficherBoiteText && !event.target.closest('.boite-content')) {
            this.setState({ afficherBoiteText: false });
        }
    }

    // Appel aprs le montage du composant composent de base 
    componentDidMount() {
        console.log("Le composant Accueil est mont.");
        document.addEventListener('click', this.handleClickOutside);
        document.addEventListener('keydown', (e) => {
            if (e.key === 'Escape') this.setState({ afficherBoiteText: false });
        });
        this.rechercheJeu.fetchInitialData((newState) => this.setState(newState));
        this.getGrilleJeuxId.fetchData();
    }

    componentWillUnmount() {
        document.removeEventListener('click', this.handleClickOutside);
    }

    async getDataGrids()
    {
        try {
            const grids = await this.client.getGrids({type: 'game', id: this.state.resultat[this.state.index].id}); // Use state.index dynamically
            console.log("debugIdJeu === ", this.state.resultat[this.state.index].id);
            console.log("Grilles rcupres :", grids);
            return grids;
        } catch (error) {
            console.error("Erreur lors de la rcupration des donnes :", error);
            throw error;
        }
    }

    mettreAjourStateDeuxiemeRequete = async () => {
        const data = await this.getDataGrids();
        this.setState({ deuxiemeRequeteDataJeu: data });
        console.log("debugstateDeuximeRequete", this.state.deuxiemeRequeteDataJeu);
    };

    handleGameSelection = (jeuSelectionne) => {
        this.setState({ jeuSelectionne });
        const navigate = this.props.navigate;
        if (navigate) {
            navigate('/voir-jeu');
        } else {
            console.error("La fonction navigate n'est pas disponible.");
        }
    };

    handleIndexSelect = (index) => {
        this.setState({ index }); 
        console.log("Index slectionn dans le parent :", index);
    };

    render() {
        return (
            <div>
                <main className="container mt-4">
                    <h1 className="presentation">Bienvenue sur le catalogue de jeux vido</h1>
                    <p className="presentation">Explorez notre collection de jeux et trouvez celui qui vous convient le mieux.</p>
                    <BarreRecherche handleSearch={this.handleSearch} />
                    <GrilleDeJeux resultats={this.state.deuxiemeRequeteDataJeu} onGameSelect={this.handleGameSelection} />
                    {this.state.afficherBoiteText && (
                        <BoiteText
                            resultats={this.state.resultat}
                            fermerBoite={this.fermerBoite}
                            mettreAjourStateDeuxiemeRequete={this.mettreAjourStateDeuxiemeRequete}
                            onIndexSelect={this.handleIndexSelect} 
                        />
                    )}
                </main>
            </div>
        );
    }
}

