import { GestionErreurs } from '../Pages/Composent/GestionErreurs';

export class RechercheJeu {
    constructor(client) {
        this.client = client; 
    }

    // Mthode pour rcuprer les donnes
    async fetchData(query) {
        try {
            const jeux = await this.client.searchGame(query);
            return jeux;
        } catch (error) {
            new GestionErreurs({
                type: 'danger',
                titre: 'Erreur de rcupration',
                message: 'Erreur lors de la rcupration des donnes : ' + error.message
            });
            throw error;
        }
    }

    // Mthode pour grer la recherche
    async handleSearch(query, setStateCallback) {
        console.log("Nouvelle recherche :", query);

        // Si l'input est vide, ne pas effectuer de requte
        if (!query.trim()) {
            new GestionErreurs({
                type: 'warning',
                titre: 'Recherche vide',
                message: 'La recherche est vide. Aucune requte effectue.'
            });
            return;
        }

        try {
            // Effectue la requte API avec la recherche de l'utilisateur
            const jeux = await this.fetchData(query);
            console.log("Rsultats de recherche :", jeux);

            // Met  jour l'tat via le callback
            setStateCallback({ resultat: jeux, rechercheEnCours: query });
        } catch (error) {
            new GestionErreurs({
                type: 'danger',
                titre: 'Erreur de recherche',
                message: 'Erreur lors de la recherche : ' + error.message
            });
        }
    }

    // Mthode pour charger les donnes initiales
    async fetchInitialData(setStateCallback) {
        console.log("Chargement initial des donnes...");
        try {
            const jeux = await this.fetchData("gta");
            setStateCallback({ resultat: jeux });
        } catch (error) {
            new GestionErreurs({
                type: 'danger',
                titre: 'Erreur initiale',
                message: 'Erreur lors du chargement initial : ' + error.message
            });
        }
    }
}