export class GetGrilleJeuxId
{
    constructor(client)
    {
        this.client = client;
    }

    async fetchData()
    {
        try {
            const grids = await this.client.getGrids({type: 'game', id: 2254}); 
            console.log("Grilles rcupres :", grids);
            return grids;
        } catch (error) {
            console.error("Erreur lors de la rcupration des donnes :", error);
            throw error;
        }
    }
}