import React from 'react';
import { ListesContext } from '../context/ListesContext';
import { GestionLocalStorage } from '../LocalStorage/localstorage';
import { withRouter } from '../utils/withRouter';

class ListePersonnalisee extends React.Component {
    static contextType = ListesContext;

    constructor(props) {
        super(props);
        this.state = {
            jeux: [],
        };
    }

    componentDidMount() {
        const { nomListe } = this.props.params;
        const listeChargee = GestionLocalStorage.charger(nomListe) || [];
        this.setState({ jeux: listeChargee });
    }

    componentDidUpdate(prevProps) {
        const { nomListe } = this.props.params;
        if (prevProps.params.nomListe !== nomListe) {
            const listeChargee = GestionLocalStorage.charger(nomListe) || [];
            this.setState({ jeux: listeChargee });
        }
    }

    supprimerJeu = (titreJeu) => {
        const { nomListe } = this.props.params;
        GestionLocalStorage.supprimerJeu(nomListe, titreJeu); 
        this.setState((prevState) => ({
            jeux: prevState.jeux.filter((jeu) => jeu.titre !== titreJeu),
        }));
    };

    render() {
        const { nomListe } = this.props.params;
        const { listes } = this.context;
        const { jeux } = this.state;

        if (!listes.includes(nomListe)) {
            return <p className="text-center">Liste non trouve.</p>;
        }

        return (
            <div className="container mt-4">
                <h1 className="text-center">Liste : {nomListe}</h1>
                <div className="row">
                    {jeux.map((jeu, index) => (
                        <div key={index} className="col-md-4 mb-4">
                            <div className="card">
                                <img src={jeu.image} className="card-img-top" alt={jeu.titre} />
                                <div className="card-body">
                                    <h5 className="card-title">{jeu.titre}</h5>
                                    <button
                                        className="btn btn-danger mt-2"
                                        onClick={() => this.supprimerJeu(jeu.titre)}
                                    >
                                        Supprimer
                                    </button>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        );
    }
}

export default withRouter(ListePersonnalisee);