import React from "react";
import { Container, Row, Col, Button } from 'react-bootstrap';
import { ListesContext } from '../context/ListesContext';
import { GestionLocalStorage } from '../LocalStorage/localstorage';
import { withRouter } from '../utils/withRouter';

class SelectionJeu extends React.Component {
    static contextType = ListesContext;

    constructor(props) {
        super(props);
        this.state = {
            jeuSelectionne: props.location.state?.jeuSelectionne || null,
        };
    }

    ajouterALaListe = (liste) => {
        const { jeuSelectionne } = this.state;
        if (!jeuSelectionne) return;

        const listeExistante = GestionLocalStorage.charger(liste) || [];
        const nouvelleListe = [...listeExistante, {
            titre: jeuSelectionne.author?.name || "Titre inconnu",
            image: jeuSelectionne.url || "https://via.placeholder.com/600x900"
        }];

        GestionLocalStorage.sauvegarder(liste, nouvelleListe);
        this.context.updateListes(liste, nouvelleListe);
        this.props.navigate(`/liste-personnalisee/${liste}`);
    };

    render() {
        const { jeuSelectionne } = this.state;
        const { listes } = this.context;

        if (!jeuSelectionne) {
            return <p className="text-center">Aucun jeu slectionn.</p>;
        }

        return (
            <section className="centered-section">
                <Container>
                    <Row>
                        <Col md={6} className="d-flex justify-content-center align-items-center min-vh-100">
                            <img
                                src={jeuSelectionne.url || "https://via.placeholder.com/600x900"}
                                alt={jeuSelectionne.notes || "Image du jeu"}
                                className="img-fluid border rounded"
                            />
                        </Col>

                        <Col md={6} className="bg-secondary text-white p-4 border rounded text-center">
                            <h1>{jeuSelectionne.author?.name || "Nom du jeu inconnu"}</h1>
                            <div className="d-flex flex-column align-items-center mt-4">
                                {listes.map((liste, index) => (
                                    <Button
                                        key={index}
                                        variant="outline-light"
                                        className="mb-3 w-75"
                                        onClick={() => this.ajouterALaListe(liste)}
                                    >
                                        Ajouter  {liste}
                                    </Button>
                                ))}
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        );
    }
}

export default withRouter(SelectionJeu);