import React from "react";
import { Container, Row, Col, Button, Form } from "react-bootstrap";
import { GestionLocalStorage } from '../LocalStorage/localstorage';

export class GestionListesPersonnalisees extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listes: ["Liste de souhaits", "Jeux  acheter", "Mes jeux favoris", "Jeux de console"],
            nouvelleListe: "",
        };
    }

    componentDidMount() {
        const savedListes = GestionLocalStorage.charger("listes");
        if (savedListes) {
            this.setState({ listes: savedListes });
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.listes !== this.state.listes) {
            GestionLocalStorage.sauvegarder("listes", this.state.listes);
        }
    }

    ajouterListe = () => {
        const { nouvelleListe, listes } = this.state;
        if (nouvelleListe.trim() !== "") {
            this.setState({
                listes: [...listes, nouvelleListe],
                nouvelleListe: "",
            });
        }
    };

    supprimerListe = (index) => {
        const nouvellesListes = this.state.listes.filter((_, i) => i !== index);
        this.setState({ listes: nouvellesListes });
    };


    handleInputChange = (event) => {
        this.setState({ nouvelleListe: event.target.value });
    };

    render() {
        const { listes, nouvelleListe } = this.state;

        return (
            <Container>
                <Row className="mb-4">
                    <Col>
                        <h1>Gestion des listes personnalises</h1>
                    </Col>
                </Row>
                <Row className="mb-4">
                    <Col>
                        <Form>
                            <Form.Group controlId="nouvelleListe">
                                <Form.Label>Ajouter une nouvelle liste</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Nom de la nouvelle liste"
                                    value={nouvelleListe}
                                    onChange={this.handleInputChange}
                                />
                            </Form.Group>
                            <Button className="mt-2" onClick={this.ajouterListe}>
                                Ajouter la liste
                            </Button>
                        </Form>
                    </Col>
                </Row>
                <Row>
                    {listes.map((liste, index) => (
                        <Col md={4} className="mb-4" key={index}>
                            <div className="p-3 border rounded bg-light d-flex justify-content-between align-items-center">
                                <h5>{liste}</h5>
                                <Button variant="danger" size="sm" onClick={() => this.supprimerListe(index)}>
                                    Supprimer
                                </Button>
                            </div>
                        </Col>
                    ))}
                </Row>
            </Container>
        );
    }
}