export class GestionLocalStorage {
    static sauvegarder(cle, valeur) {
        try {
            localStorage.setItem(cle, JSON.stringify(valeur));
        } catch (erreur) {
            console.error("Erreur lors de la sauvegarde dans le localStorage :", erreur);
        }
    }

    static charger(cle) {
        try {
            const valeur = localStorage.getItem(cle);
            return valeur ? JSON.parse(valeur) : null;
        } catch (erreur) {
            console.error("Erreur lors du chargement depuis le localStorage :", erreur);
            return null;
        }
    }

    static supprimer(cle) {
        try {
            localStorage.removeItem(cle);
        } catch (erreur) {
            console.error("Erreur lors de la suppression dans le localStorage :", erreur);
        }
    }

    static supprimerJeu(nomListe, titreJeu) {
        try {
            const liste = this.charger(nomListe);
            if (liste) {
                const nouvelleListe = liste.filter(jeu => jeu.titre !== titreJeu);
                this.sauvegarder(nomListe, nouvelleListe);
            }
        } catch (erreur) {
            console.error("Erreur lors de la suppression du jeu dans la liste depuis le localStorage :", erreur);
        }
    }
}