import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Accueil } from './Pages/Accueil';
import { BarreNavigation } from './Pages/Composent/BarreNavigation';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import SGDB from "steamgriddb";
import SelectionJeu from './Pages/SelectionJeu';
import { GestionListesPersonnalisees } from './Pages/ParametreListe';
import { PiedDePage } from './Pages/Composent/PiedDePage';
import { RechercheJeu } from './RequestApi/RechercheJeu'; 
import { GetGrilleJeuxId } from './RequestApi/getGrilleJeuxId';
import { PageNotFound } from './Pages/PageNotFound'; 
import ListePersonnalisee from './Pages/ListePersonnalisee';
import { ListesProvider } from './context/ListesContext';

export class App extends React.Component {

    constructor(props) {
        super(props);
        this.client = new SGDB('170213bde18b84a0d253378214dbae01');
        this.rechercheJeu = new RechercheJeu(this.client);
        this.getGrilleJeuxId = new GetGrilleJeuxId(this.client);

        this.state = {
            jeuSelectionne: null, 
        };
    }

    render() {
        return (
            <ListesProvider>
                <div className="d-flex flex-column min-vh-100">
                    <Router>
                        <BarreNavigation />
                        <div className="flex-grow-1">
                            <Routes>
                                <Route path="/" element={<Accueil client={this.client} rechercheJeu={this.rechercheJeu} getGrilleJeuxId={this.getGrilleJeuxId} />} />
                                <Route path="/liste-personnalisee" element={<GestionListesPersonnalisees />} />
                                <Route path="/liste-personnalisee/:nomListe" element={<ListePersonnalisee />} />
                                <Route path ="/voir-jeu" element={<SelectionJeu jeuSelectionne={this.state.jeuSelectionne} />} />
                                <Route path="*" element={<PageNotFound />} />
                            </Routes>
                        </div>
                        <PiedDePage />
                    </Router>
                </div>
            </ListesProvider>
        );
    }
}

export default App;