import React from 'react';
import { Alert } from 'react-bootstrap';
import './GestionErreurs.css';

export class GestionErreurs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: true
        };
    }

    componentDidMount() {
        if (this.props.autoHide) {
            setTimeout(() => {
                this.setState({ visible: false });
            }, this.props.duree || 5000);
        }
    }

    render() {
        if (!this.state.visible) {
            return null;
        }

        return (
            <div className="gestion-erreurs-container">
                <Alert 
                    variant={this.props.type || 'danger'}
                    onClose={() => this.setState({ visible: false })}
                    dismissible
                    className="gestion-erreurs-content"
                >
                    <Alert.Heading className="gestion-erreurs-header">
                        {this.props.titre || 'Erreur'}
                    </Alert.Heading>
                    <p className="gestion-erreurs-message">
                        {this.props.message}
                    </p>
                </Alert>
            </div>
        );
    }
}