import React from 'react';
import { ListGroup } from 'react-bootstrap';
import './BoiteText.css';

export class BoiteText extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            resultat: null, 
        };
    }

    handleItemClick = (index) => {
        // Vrifiez que resultats est un tableau valide et que l'index est correct
        if (Array.isArray(this.props.resultats) && index >= 0 && index < this.props.resultats.length) {
            const selectedResult = this.props.resultats[index];

            // Mettez  jour l'tat avec l'lment slectionn
            this.setState({ resultat: selectedResult }, () => {
                console.log('tat mis  jour :', this.state.resultat);

                if (typeof this.props.fermerBoite === 'function') {
                    this.props.fermerBoite();
                } else {
                    console.error("fermerBoite n'est pas une fonction valide.");
                }

                if (typeof this.props.mettreAjourStateDeuxiemeRequete === 'function') {
                    this.props.mettreAjourStateDeuxiemeRequete();
                } else {
                    console.error("mettreAjourStateDeuxiemeRequete n'est pas une fonction valide.");
                }

                if (typeof this.props.onIndexSelect === 'function') {
                    this.props.onIndexSelect(index); // Update the index in the parent component
                } else {
                    console.error("onIndexSelect n'est pas une fonction valide.");
                }
            });

            console.log('Index slectionn :', index);
            console.log('lment slectionn :', selectedResult);
        } else {
            console.error("Index invalide ou resultats n'est pas un tableau.");
        }
    };

    render() {
        const { resultat } = this.state;

        return (
            <div className="boite-overlay">
                <div className="boite-content">
                    <ListGroup>
                        {this.props.resultats && this.props.resultats.map((item, index) => (
                            <ListGroup.Item
                                key={item.id || index}
                                action
                                onClick={() => this.handleItemClick(index)} 
                                className={`boite-item ${resultat === item ? 'active' : ''}`} 
                            >
                                <span>{item.name}</span>
                                <small className="text-muted">#{index}</small>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                </div>
            </div>
        );
    }
}