import React from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Container, Nav } from 'react-bootstrap';
import { GestionLocalStorage } from '../../LocalStorage/localstorage.js';

export class BarreNavigation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listes: [],
        };
    }

    componentDidMount() {
        this.updateListes();
        window.addEventListener('storage', this.handleStorageChange); 
    }

    componentWillUnmount() {
        window.removeEventListener('storage', this.handleStorageChange);
    }

    handleStorageChange = (event) => {
        if (event.key === "listes") {
            this.updateListes();
        }
    };

    updateListes = () => {
        const listes = GestionLocalStorage.charger("listes") || [];
        this.setState({ listes });
    };

    render() {
        const { listes } = this.state;

        return (
            <header>
                <Navbar expand="lg" bg="dark" variant="dark">
                    <Container>
                        <Navbar.Brand as={Link} to="/">Jeux Vido</Navbar.Brand>
                        <Navbar.Toggle aria-controls="navbarNav" />
                        <Navbar.Collapse id="navbarNav">
                            <Nav className="me-auto">
                                <Nav.Link as={Link} to="/">Accueil</Nav.Link>
                                <Nav.Link as={Link} to="/liste-personnalisee">Paramtre de la liste</Nav.Link>
                                <Nav.Item className="dropdown">
                                    <Nav.Link className="dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Listes personnalises
                                    </Nav.Link>
                                    <ul className="dropdown-menu">
                                        {listes.length > 0 ? (
                                            listes.map((liste, index) => (
                                                <li key={index}>
                                                    <Nav.Link className="dropdown-item text-dark" as={Link} to={`/liste-personnalisee/${liste}`}>{liste}</Nav.Link>
                                                </li>
                                            ))
                                        ) : (
                                            <li>
                                                <span className="dropdown-item text-dark">Aucune liste disponible</span>
                                            </li>
                                        )}
                                    </ul>
                                </Nav.Item>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </header>
        );
    }
}