import React from "react";
import { Card, Button, Container, Row, Col } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

class GrilleDeJeux extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            jeuSelectionne: null,
        };
        this.navigate = props.navigate;
    }

    handleGameSelect = (item) => {
        this.setState({ jeuSelectionne: item });
        this.navigate("/voir-jeu", { state: { jeuSelectionne: item } });
    };

    renderGrilles() {
        if (!Array.isArray(this.props.resultats) || this.props.resultats.length === 0) {
            return <p>Aucune grille disponible.</p>;
        }

        return this.props.resultats.map((item, index) => (
            <Col md={4} className="mb-4" key={item.id || index}>
                <Card>
                    <Card.Img
                        variant="top"
                        src={item.thumb || item.url} 
                        alt={`Grille ${index}`}
                    />
                    <Card.Body>
                        <Card.Title>{item.author?.name || "Auteur inconnu"}</Card.Title>
                        <Button 
                            variant="primary" 
                            onClick={() => this.handleGameSelect(item)}
                        >
                            Voir le jeu
                        </Button>
                    </Card.Body>
                </Card>
            </Col>
        ));
    }

    render() {
        return (
            <Container>
                <Row>
                    {this.renderGrilles()}
                </Row>
            </Container>
        );
    }
}

const GrilleDeJeuxWrapper = (props) => {
    const navigate = useNavigate();
    return <GrilleDeJeux {...props} navigate={navigate} />;
};

export default GrilleDeJeuxWrapper;