import React, { createContext } from 'react';
import { GestionLocalStorage } from '../LocalStorage/localstorage';

export const ListesContext = createContext();

export class ListesProvider extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listes: GestionLocalStorage.charger('listes') || [],
        };
    }

    updateListes = (nomListe, nouvelleListe) => {
        GestionLocalStorage.sauvegarder(nomListe, nouvelleListe);
        this.setState((prevState) => {
            if (!prevState.listes.includes(nomListe)) {
                return { listes: [...prevState.listes, nomListe] };
            }
            return prevState;
        });
    };

    render() {
        return (
            <ListesContext.Provider
                value={{
                    listes: this.state.listes,
                    updateListes: this.updateListes,
                }}
            >
                {this.props.children}
            </ListesContext.Provider>
        );
    }
}